.header {
  /* position: relative; */
  width: 100%;
  /* top: 0; */
  position: sticky;
  top: 0;
  background-color: rgb(240, 237, 237);
  border-bottom: 1px solid #ccc;
  z-index: 1000;
}

.header nav {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10px 20px;
}

.header nav ul {
  list-style: none;
  display: flex;
  justify-content: center;
  padding: 0;
  margin: 0;
}

.header nav ul li {
  margin: 0 15px;
  cursor: pointer;
  font-size: medium;
  font-weight: bold;
  padding: 10px;
  border-radius: 3px;
}

.add-to-week {
  margin-left: auto;
  margin-right: 20px;
}
